.result{
    display: block;
    height: 150px;
    position: relative;
    background: rgb(28, 235, 66);
}
.res-con{
    height: 100vh;
}
.res{
    font-weight: 600;
}
.ques-content{
    background: rgba(53, 49, 75, 0.89);
    color: #fff;
}
.options{
    padding: 4px 3px;
    text-align: center;
    cursor: pointer;
    margin: 7px 1px;
}
.options:hover{
    background: rgb(36, 242, 67);
    color: rgb(36, 36, 231);
}
.selected{
    background: rgb(36, 242, 67);
    color: rgb(36, 36, 231);
}